import helper from '../../common/helper.js';
export default {
	data() {
		return {
			userinfo: '' //商家信息
		}
	},
	onLoad() {
	   this.MerInfo()
	},
	onShow() {

	},
	onPullDownRefresh: function() { //下拉刷新
		this.MerInfo(true);
	},
	methods: {
		// 获取商家信息
		MerInfo(isdown) {
			helper.islogin_Mer(true);
			uni.showLoading({
				mask: true,
				title: '加载中'
			})
			var userId = helper.getstate().userid;
			uni.request({
				url: helper.websiteUrl + 'mindex/getindex',
				method: 'GET',
				data: helper.postdata({
					merchantid: userId,
				}),
				success: (res) => {
					if (res.data.code == 200) {
						this.userinfo = res.data.data.baseinfo;
					} else {
						helper.goout(res.data.info);
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					uni.hideLoading();
				},
				fail() {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常，请下拉刷新'
					});
				},
				complete: () => {
					if (isdown) {
						uni.stopPullDownRefresh(); //刷新停止
					}
				}
			});
		},
		// 打开充值中心
		opencher() {
			uni.navigateTo({
				url: 'voucher'
			});
		},
		// 打开发布活动
		openNew() {
			uni.navigateTo({
				url: '../TaskList/TaskList'
			});
		},
		// 核销活动
		nucleus() {
			uni.scanCode({
				success: function(res) {
					var canid = res.result //扫描出的会员领取活动id
					// uni.request({ // 请求核销接口
					// 	url: helper.websiteUrl + 'mtask/writeoffs',
					// 	method: 'GET',
					// 	data: helper.postdata({
					// 		Mytaskid: canid,
					// 		mid: helper.getstate().userid
					// 	}),
					// 	success: (res) => {
					// 		if (res.data.code == 200) {
					// 			uni.showModal({
					// 				title: '提示',
					// 				content: res.data.info,
					// 			});
					// 		} else {
					// 			helper.goout(res.data.info);
					// 			uni.showModal({
					// 				title: '提示',
					// 				content: res.data.info,
					// 			});
					// 		}
					// 		uni.hideLoading();
					// 	},
					// 	fail() {
					// 		uni.hideLoading();
					// 	},
					// });
				},
				fail: function(res) {
					uni.showToast({
						icon: 'none',
						title: '网络异常，请重新扫描'
					});
				}
			})
		},
		//核销
		cancel() {

		},
		// 打开商家活动
		opentask() {
			uni.redirectTo({
				url: 'MerTask'
			});
		},
		// 打开商家信息
		openInfo() {
			uni.redirectTo({
				url: 'MerInfo'
			});
		},
		// 打开已领取的活动
		openylq() {
			uni.navigateTo({
				url: 'YetUnclaimed'
			});
		},
		// 打开待领取的活动
		opendlq() {
			uni.navigateTo({
